<template>
  <div class="one-logistics">
    <adt-search-folder
      :searchRouter="{query: {accountName: search.accountName, organizationId: search.organizationId, phoneNumber: search.phoneNumber}}"
      :isTitle="false"
      :isStatus="true"
    >
      <template slot="status">
        <div class="status">
          <div class="status-left">
            <p>状态:</p>
            <span
              v-for="(status, id) in auditStatusList"
              :key="id"
              @click="auditStatus(id)"
              :class="{test: auditStatusNum == id}"
            >{{status.status}}</span>
          </div>
        </div>
      </template>
      <template slot="always-show">
        <Row>
          <Col span="4" class="search-condition min-width">
            <span>起运地：</span>
            <Input v-model="search.accountName" clearable></Input>
          </Col>
          <Col span="4" class="search-condition min-width">
            <span>目的地：</span>
            <Input v-model="search.accountName" clearable></Input>
          </Col>
          <Col span="14" class="search-condition min-width transit">
            <span class="transit-tit">中转地：</span>
            <Select
              v-model="transitModal"
              filterable
              remote
              :remote-method="transitRemote"
              :loading="transitLoading"
              @on-select="onChangeClick"
            >
              <Option
                v-for="(option, index) in transitData"
                :value="option.value"
                :key="index"
              >{{option.label}}</Option>
            </Select>
            <p class="select-value">
              <span v-for="(value, index) in transitSelect" :key="index">
                {{value}}
                <Icon type="ios-close-circle" @click="delTransit(index)" />
              </span>
            </p>
          </Col>
        </Row>
        <br />
        <Row>
          <Col span="4" class="search-condition min-width">
            <span @click="hh()">合同号：</span>
            <Input v-model="search.accountName" clearable></Input>
          </Col>
        </Row>
      </template>
    </adt-search-folder>
    <div class="btn-pagina">
      <AdtButton class="btn-operating" :allDelShow="true" :selectList="selectList">
        <!-- <Button>
          批量删除
          <Icon type="ios-close-circle-outline" />
        </Button>-->
      </AdtButton>
      <adt-paginator
        @getPages="getPages"
        class="pagination"
        :currentPage="~~page"
        :total="~~total"
        path="/authority-manage/user-manage"
      />
    </div>
    <div class="public-component-line"></div>
    <Col class="public-component-card" span="12" v-for="(logistics, id) in listData" :key="id">
      <adt-list-item :logistics="logistics" @checkData="checkData" :isFreight="true">
        <template slot="operat">
          <Poptip placement="right-start" class="card-icon clearfix">
            <span>编辑</span>
            <Icon type="md-arrow-dropdown" />
            <div class="card-poptip-font" slot="content">
            </div>
          </Poptip>
        </template>
      </adt-list-item>
    </Col>
  </div>
</template>

<script>
import paginator from "@/mixins/paginator";
import AdtListItem from "@/components/adt-list-item/Index";
export default {
  mixins: [paginator],
  components: {
    AdtListItem
  },
  data() {
    return {
      listData: [
        {
          statusCode: "disagree",
          status: "销售已反馈",
          logisticsPlanNumber: "物流方案编号1",
          time: "时效",
          price: "价格",
          location: [
            {
              city: "重庆1",
              transport: "空运",
              type: "国内集装箱"
            },
            {
              city: "香港",
              transport: "空运",
              type: "国内集装箱"
            },
            {
              city: "纽约",
              transport: "空运",
              type: "国内集装箱"
            }
          ]
        },
        {
          statusCode: "disagree",
          status: "销售已反馈",
          logisticsPlanNumber: "物流方案编号3",
          time: "时效",
          price: "价格",
          location: [
            {
              city: "香港",
              transport: "空运",
              type: "国内集装箱"
            },
            {
              city: "纽约",
              transport: "空运",
              type: "国内集装箱"
            }
          ]
        }
      ],
      selectList: {},
      transitModal: "",
      transitLoading: false,
      transitData: [],
      list: ["加拿大", "北级", "北京", "加州", "哈尔滨", "邯郸", "韩国"],
      search: {},
      auditStatusNum: "默认值",
      auditStatusList: [
        { code: "unreviewed", status: "已下单" },
        { code: "stock", status: "已接受" },
        { code: "stock", status: "已拒绝" },
        { code: "stock", status: "已取消" },
        { code: "stock", status: "未确定计划" },
        { code: "stock", status: "不同意计划" },
        { code: "stock", status: "待货好" },
        { code: "progress", status: "进行中" },
        { code: "progress", status: "已完成" },
        { code: "progress", status: "异常进行中" },
        { code: "progress", status: "异常处理中" },
        { code: "progress", status: "已撤销" },
        { code: "progress", status: "已支付" }
      ],
      transitSelect: [],
      checkList: [],
      newList: []
    };
  },
  methods: {
    hh(){
      // this.$router.push('/logisstics-commission/add-single')
      this.$router.push('/logisstics-commission/singleDetail')
    },
    checkData(data, status) {
      if (data && !status) {
        return data;
      }
      this.checkList.push(data);
      console.log(this.checkList);
      // this.checkList.map(train=>{
      //     this.newList.push(train)
      // })
      // this.newList = [...new Set(this.newList)]
    },
    getPages(page) {
      this.page = page;
    },
    dataInit() {
      this.getData();
    },
    getData() {},
    // 删除
    auditStatus(index) {
      // 控制状态切换传值
      this.auditStatusNum = index;
    },
    onChangeClick(val) {
      if (this.transitSelect.length >= 5) {
        console.log("不能超过5个中转地");
        return;
      }
      this.transitSelect.push(val.value);
    },
    transitRemote(val) {
      if (val !== "") {
        this.transitLoading = true;
        setTimeout(() => {
          this.transitLoading = false;
          const list = this.list.map(item => {
            return {
              value: item,
              label: item
            };
          });
          this.transitData = list.filter(
            item => item.label.toLowerCase().indexOf(val.toLowerCase()) > -1
          );
        }, 200);
      } else {
        this.transitData = [];
      }
    },
    delTransit(index) {
      this.transitSelect.splice(index, 1);
    }
  }
};
</script>

<style lang="scss" scoped>
.one-logistics {
  /deep/.ivu-tabs-bar {
    border-bottom: none;
  }
  /deep/.ivu-tabs-ink-bar {
    height: 0;
  }
  /deep/.ivu-tabs-nav .ivu-tabs-tab-active {
    color: #0fb0b8;
  }
  /deep/.ivu-tabs-nav {
    font-size: 16px;
    font-weight: bold;
  }
  .btn-pagina {
    display: flex;
    justify-content: space-between;
  }
}

.test {
  background-color: $filling-color;
}

span {
  width: 120px;
  line-height: 30px;
}
p {
  display: inline-block;
  font-size: $font-size-large;
  font-weight: bolder;
  font-family: $base-font-family;
}
.search-condition {
  margin-right: 30px;
  span {
    width: 200px !important;
  }
}
.min-width {
  span {
    width: 60px !important;
  }
  .ivu-input-wrapper {
    width: 60%;
  }
}
.ivu-modal-body {
  .search-condition {
    span {
      width: 180px !important;
    }
  }
}
// 审核状态、启用状态
.status {
  display: flex;
  span {
    padding: 5px 10px;
    margin: 5px 10px;
    cursor: pointer;
    color: $main-font-color;
    &:hover {
      background-color: $filling-color;
    }
    &:active {
      background-color: $filling-color;
    }
  }
}
.line {
  border: 0.5px dashed $border-color;
  margin: 20px 0;
}

.search-condition {
  display: flex;
}
.modal-code {
  font-size: 14px;
  font-weight: 400;
  span {
    margin-left: 10px;
  }
}
.line-margin {
  margin-left: 30px;
}
.modal-css {
  margin-left: 0;
}
.icon-action {
  margin: 0 4px;
}
.title {
  font-size: 16px;
  font-weight: bold;
  color: rgba(102, 102, 102, 1);
  line-height: 57px;
  cursor: pointer;
  padding: 0 45px;
  .clo {
    margin: 0 56px;
    width: 10px;
  }
  .title-active {
    color: #0fb0b8;
  }
  span {
    display: inline-block;
  }
}
</style>
